.contacts {
  padding-top: 170px;
}

.contacts__wrap {
  display: flex;
}

.contacts__list {
  max-width: 380px;
  width: 100%;
  list-style-type: none;
  padding-top: 25px;
}

.contacts__item + .contacts__item {
  margin-top: 63px;
}

.contacts__link {
  display: inline-block;
  font-size: 22px;
  font-weight: 400;
  padding-left: 40px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 22px 22px;
}

.contacts__copyright {
  position: absolute;
  bottom: 40px;
  font-size: 14px;
  color: #B2B2B2;
  right: 0;
}

.contacts__map {
  position: relative;
  background-color: #fdfdfd;
  max-width: 630px;
  width: 100%;
  padding-bottom: 26.3%;
  margin-left: 110px;
}

.contacts__map-trigger {
  display: none;
  margin-top: 25px;
  text-align: left;
  font-size: 15px;
  color: #979CA1;
  padding-bottom: 3px;
  transition: color 0.3s ease-out 0s;
}

.contacts__map-trigger::after {
  content: '';
  display: inline-block;
  position: absolute;
  width: 100%;
  bottom: 1px;
  left: 0;
  height: 1px;
  background-color: #979CA1;
  transition: background-color 0.3s ease-out 0s;
}

.contacts__map > * {
  filter: grayscale(70%);
  position: absolute;
  width: 100%;
  height: 100%;
}

.contacts__form {
  display: flex;
  align-items: flex-end;
  margin-top: 75px;
  background: rgba(19, 24, 28, 0.68);
  max-width: 1210px;
  padding: 20px 40px 20px 0px;
  position: relative;
}

.contacts__form::before {
  content: '';
  display: block;
  width: 400px;
  height: 100%;
  position: absolute;
  right: 100%;
  top: 0;
  background: rgba(19, 24, 28, 0.68);
}

.contacts__form-text {
  max-width: 300px;
  font-weight: 400;
}

.contacts__form-title {
  font-size: 35px;
  line-height: 37px;
  font-weight: 300;
  max-width: 280px;
  margin-bottom: 14px;
}

.contacts__form-body {
  margin: 0px 43px 0 63px;
  width: 100%;
  max-width: 600px;
}

@media screen and (max-width: 1640px) {
  .contacts__form {
    margin-top: 20px;
    padding: 15px 30px;
  }
  .contacts__form-title {
    font-size: 28px;
    line-height: 32px;
  }
  .contacts__form-body {
    margin: 0px 30px;
  }
}

@media screen and (max-width: 1260px) {
  .contacts {
    padding-top: 70px;
  }
  .contacts__map {
    min-height: 250px;
    margin-left: 30px;
  }
}

@media screen and (max-width: 920px) {
  .contacts {
    padding-bottom: 40px;
  }
  .contacts__copyright {
    font-size: 10px;
    text-align: center;
    display: inline-block;
    bottom: 5px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
  }
  .contacts__list {
    padding-top: 0;
  }
  .contacts__link {
    font-size: 18px;
  }
  .contacts__item + .contacts__item {
    margin-top: 30px;
  }
  .contacts__wrap {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 15px;
  }
  .contacts__form {
    padding-bottom: 50px;
  }
  .contacts__map {
    display: none;
  }
  .contacts__map-trigger {
    display: inline-block;
  }
}

@media screen and (max-width: 895px) {
  .contacts {
    padding-top: 100px;
  }
  .contacts__form {
    flex-direction: column;
    align-items: flex-start;
    margin-right: 20px;
  }
  .contacts__form .form__submit {
    align-self: initial;
  }
  .contacts__form-body {
    margin: 25px 0px 10px;
  }
  .contacts__form-title {
    font-size: 20px;
    line-height: 26px;
  }
  .contacts__form-title, .contacts__form-text {
    max-width: none;
  }
  .contacts__form-subtitle {
    display: none;
  }
}

@media screen and (max-width: 520px) {
  .contacts__map-trigger {
    margin-top: 15px;
  }
  .contacts__form {
    padding-bottom: 80px;
  }
  .contacts__form .form__submit {
    align-self: center;
  }
}

@media screen and (max-height: 700px) {
  .contacts {
    padding-top: 110px;
  }
  .contacts__item + .contacts__item {
    margin-top: 10px;
  }
}

@media screen and (max-height: 600px) {
  .contacts {
    overflow-y: auto;
  }
}
