.main-title {
  list-style-type: none;
  z-index: 3;
  position: fixed;
  top: 165px;
  left: 260px;
  width: 100%;
}

.main-title__item {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  font-size: 16px;
  color: #ffffff;
  font-weight: 500;
  transition: opacity 0.3s ease-out 0s;
  max-width: 165px;
}

.main-title__item::before {
  content: '';
  position: absolute;
  display: block;
  bottom: -8px;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.3s ease-out 0s;
}

.main-title__item.active {
  opacity: 1;
  pointer-events: all;
}

.main-title__item.active::before {
  transform: scaleX(1);
}

@media screen and (max-width: 1600px) {
  .main-title {
    left: 180px;
  }
}

@media screen and (max-width: 1300px) {
  .main-title {
    top: 120px;
    left: 15px;
  }
}

@media screen and (max-width: 1060px) {
  .main-title {
    left: 15px;
  }
}

@media screen and (max-width: 920px) {
  .main-title {
    top: 120px;
  }
}

@media screen and (max-height: 700px) {
  .main-title {
    top: 80px;
  }
  .main-title__item {
    font-size: 14px;
    line-height: 18px;
  }
}
