@import "./varaibles";

.about {
    padding-top: 185px;
    &__wrap {
        display: flex;
        align-items: flex-start; }
    &__body + &__benefits {
        margin-left: 100px; }
    &__body {
        width: 100%;
        max-width: 620px; }
    &__benefits {
        max-width: 570px;
        width: 100%; }
    &__header {
        display: flex;
        align-items: flex-end;
        margin-bottom: 45px; }
    &__slogan {
        display: inline-block;
        max-width: 105px;
        font-size: 14px;
        line-height: 18px;
        padding-left: 15px;
        margin-left: 15px;
        border-left: 2px solid $text-color; }
    &__content {
        font-size: 16px;
        line-height: 170%;
        margin-bottom: 30px;
        p {
            color: inherit;
            line-height: inherit;
            font-size: inherit;
            font-weight: inherit; }
        p + p {
            margin-top: 1.4em; } }
    &__request {
        font-size: 14px;
        font-weight: 400;
        text-align: left;
        span {
            display: inline-block;
            position: relative;
            vertical-align: middle;
            border-radius: 50%;
            height: 25px;
            width: 25px;
            background-color: #FF9A00;
            margin-right: 25px;
            flex-shrink: 0;
            &::before, &::after {
                content: '';
                display: inline-block;
                width: 14px;
                height: 2px;
                background-color: #fff;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                @include transition(transform); }
            &::before {
                transform: translate(-50%, -50%) rotate(90deg); }
            &::after {
                transform: translate(-50%, -50%) rotate(0deg); } }
        &:hover {
            span {
                &::before {
                    transform: translate(-50%, -50%) rotate(360deg); }
                &::after {
                    transform: translate(-50%, -50%) rotate(90deg); } } } }
    &__benefits {
        list-style: none; }
    &__benefit {
        &-title {
            display: block;
            margin-bottom: 10px;
            font-size: 16px;
            font-weight: 500;
            color: $text-color-accent; }
        &-text {
            font-size: 16px;
            line-height: 18px; }
        & + & {
            margin-top: 25px; } } }

@media screen and ( max-width: 1600px ) {
    .about {
        padding-top: 100px; } }

@media screen and ( max-width: 1260px ) {
    .about {
        padding-top: 70px;
        &__header {
            margin-bottom: 25px; }
        &__body + &__benefits {
            margin-left: 50px; } } }

@media screen and ( max-width: 780px ) {
    .about {
        padding-top: 160px;
        &__wrap {
            justify-content: center;
            align-items: center; }
        &__benefits {
            display: none; } } }

@media screen and ( max-width: 720px ) {
    .about {
        &__logo {
            max-width: 80px; }
        &__slogan {
            font-size: 10px;
            line-height: 12px; }
        &__header {
            margin-bottom: 15px; }
        &__content {
            font-size: 14px; } } }

@media screen and ( max-width: 410px ) {
    .about {
        &__content {
            line-height: 19px; } } }

@media screen and ( max-width: 400px ) and (max-height: 750px) {
    .about {
        overflow-y: auto;
        padding-bottom: 70px;
        padding-top: 130px; } }


