@import "./varaibles";

.dropdown {
    position: relative;
    &__list {
        position: absolute;
        @include transition(all);
        opacity: 0;
        visibility: hidden;
        list-style-type: none;
        &.active {
            opacity: 1;
            visibility: visible; } }
    .js-dropdown-select, select {
        opacity: 0;
        position: absolute;
        visibility: hidden;
        font-size: 0; } }

