@import "./varaibles";

.services {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 70px;
    &__list {
        list-style-type: none;
        width: 100%;
        max-width: 1000px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
        column-gap: 40px;
        row-gap: 140px; } }

@media screen and ( max-width: 920px ) {
    .services {
        align-items: flex-start;
        padding-top: 100px;
        &__list {
            justify-items: center;
            grid-template-columns: minmax(0, 560px);
            column-gap: 50px;
            row-gap: 40px; } } }

@media screen and ( max-height: 680px ) {
    .services {
        &__list {
            row-gap: 20px; } } }

@media screen and (max-height: 700px) {
    .services {
        padding-top: 160px;
        &__list {
            padding-bottom: 40px; } } }

@media screen and (max-height: 600px) {
    .services {
        padding-top: 140px;
        &__list {
            row-gap: 17px; } } }
