.main {
  display: flex;
  position: relative;
  grid-area: main;
  padding-left: 370px;
  min-width: 0;
}

.main__control {
  position: absolute;
  color: #4F5B5F;
  font-size: 12px;
  text-align: left;
  z-index: 4;
  overflow: visible;
  transition: all 0.3s ease-out 0s;
  transform: translate(-50%, 0%) rotate(-90deg);
  bottom: 140px;
  left: 70px;
  padding: 5px 0px;
  pointer-events: all;
  visibility: visible;
}

.main__control svg {
  position: absolute;
  top: 50%;
  left: -20px;
  fill: #4F5B5F;
  stroke: #4F5B5F;
  transform: translate(-100%, -50%) rotate(90deg);
  transition: fill 0.3s ease-out 0s;
  transition-duration: .2s;
}

.main__control:hover {
  color: #ECB869;
}

.main__control:hover svg {
  fill: #ECB869;
  stroke: #ECB869;
}

.main__control_hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.main__control_back svg {
  transform: translate(-100%, -50%) rotate(-90deg);
}

.main > .swiper-wrapper > .swiper-slide {
  transition: opacity 0.3s ease-out 0s;
  opacity: 0;
}

.main > .swiper-wrapper > .swiper-slide-active {
  opacity: 1;
}

@media screen and (max-width: 1300px) {
  .main {
    padding-left: 100px;
  }
}

@media screen and (max-width: 1060px) {
  .main {
    padding-left: 0px;
  }
  .main__control {
    transform: translate(-100%, 180%) rotate(-90deg);
  }
  .main__control_back {
    transform: translate(-150%, 180%) rotate(-90deg);
  }
}

@media screen and (max-width: 920px) {
  .main {
    padding-top: 80px;
    overflow: hidden;
  }
  .main__control {
    font-size: 0;
    width: 24px;
    height: 35px;
    background-image: url(../img/slide-next-m.png);
    background-size: cover;
    background-repeat: no-repeat;
    transform: translate(0%, 0%) rotate(0deg);
    left: 15px;
    bottom: 20px;
  }
  .main__control_back {
    background-image: url(../img/slide-back-m.png);
    transform: translate(0%, 0%) rotate(0deg);
  }
  .main__control svg {
    display: none;
  }
}

@media screen and (max-height: 700px) {
  .main {
    padding-top: 10px;
  }
}
