@import "./varaibles";

.faq {
    padding-top: 130px;
    &__wrap {
        display: flex; }
    &__questions {
        list-style-type: none; }
    &__trigger {
        max-width: 510px;
        padding: 15px 25px;
        font-size: 19px;
        line-height: 30px;
        text-align: left;
        @include transition(all);
        &.active, &:hover {
            color: $text-color-accent;
            background-color: rgba(19, 24, 28, 0.68); } }
    &__question {
        & + & {
            margin-top: 35px; } }
    &__answers-list {
        list-style-type: none; }
    &__answer {
        position: absolute;
        height: 100%;
        background-color: #fff;
        max-width: 560px;
        width: 100%;
        padding: 0px 50px;
        @include transition(opacity);
        &.active {
            z-index: 3; }
        .popup__close {
            display: none; }
        &:not(.active) {
            opacity: 0; }
        &::before {
            content: '';
            display: block;
            width: 100%;
            position: absolute;
            height: 400px;
            left: 0;
            bottom: 100%;
            background-color: inherit;
            z-index: -1; }
        &-controls {
            display: flex;
            align-items: center;
            justify-content: flex-end; }
        &-control {
            display: none;
            height: 30px;
            width: 30px;
            border-radius: 50%;
            background-color: #DBDBDB;
            @include transition(background-color);
            background-size: 30%;
            background-image: url(../img/swiper-arrow.svg);
            background-repeat: no-repeat;
            background-position: 55% center;
            &_prev {}
            &_next {
                margin-left: 10px;
                transform: rotate(180deg); }
            &:hover {
                background-color: $text-color-accent; } }
        &-title {
            font-size: 16px;
            font-weight: 500;
            color: $text-color-accent;
            margin-bottom: 3px; }
        &-content {
            font-size: 16px;
            line-height: 210%;
            color: #000000;
            p {
                color: inherit;
                line-height: inherit;
                font-size: inherit; }
            p + p {
                margin-top: 1.2em; } } } }

@media screen and ( max-width: 1360px ) {
    .faq {
        margin-left: 0px; } }

@media screen and ( max-width: 1260px ) {
    .faq {
        margin-left: 0px;
        &__questions {
            max-width: 440px; }
        &__answer {
            max-width: 440px;
            padding: 0px 30px; }
        &__question {
            & + & {
                margin-top: 10px; } } } }

@media screen and ( max-width: 1060px ) {
    .faq {
        margin-left: 0px;
        &__trigger {
            font-size: 16px;
            line-height: 26px; } } }

@media screen and ( max-width: 920px ) {
    .faq {
        padding-top: 130px;
        position: relative;
        &__wrap {
            flex-direction: column;
            align-items: stretch; }
        &__questions {
            margin-right: -15px; }
        &__questions, &__answer {
            max-width: none; }
        &__answers-list {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%; }
        &__trigger {
            background-color: rgba(19, 24, 28, 0.68);
            width: 100%;
            max-width: none;
            font-size: 16px;
            line-height: 20px; }
        &__answer {
            display: block;
            height: 100vh;
            overflow-y: auto;
            left: 0;
            top: 0;
            z-index: 10;
            opacity: 1 !important;
            transform: translateX(100%);
            background-color: #fff;
            @include transition(all);
            visibility: hidden;
            padding: 35px 20px;
            &::before {
                display: none; }
            .popup__close, &-control {
                display: inline-block; }
            &.active {
                visibility: visible;
                transform: translateX(0%); } } }
    .container {
        position: static; } }

@media screen and ( max-width: 700px ) {
    .faq {
        padding-top: 120px; } }

@media screen and ( max-height: 700px ) {
    .faq {
        padding-top: 140px;
        &__trigger {
            font-size: 14px;
            line-height: 18px; } } }

@media screen and ( max-height: 600px ) {
    .faq {} }

