.about {
  padding-top: 185px;
}

.about__wrap {
  display: flex;
  align-items: flex-start;
}

.about__body + .about__benefits {
  margin-left: 100px;
}

.about__body {
  width: 100%;
  max-width: 620px;
}

.about__benefits {
  max-width: 570px;
  width: 100%;
}

.about__header {
  display: flex;
  align-items: flex-end;
  margin-bottom: 45px;
}

.about__slogan {
  display: inline-block;
  max-width: 105px;
  font-size: 14px;
  line-height: 18px;
  padding-left: 15px;
  margin-left: 15px;
  border-left: 2px solid #ffffff;
}

.about__content {
  font-size: 16px;
  line-height: 170%;
  margin-bottom: 30px;
}

.about__content p {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
  font-weight: inherit;
}

.about__content p + p {
  margin-top: 1.4em;
}

.about__request {
  font-size: 14px;
  font-weight: 400;
  text-align: left;
}

.about__request span {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  background-color: #FF9A00;
  margin-right: 25px;
  flex-shrink: 0;
}

.about__request span::before, .about__request span::after {
  content: '';
  display: inline-block;
  width: 14px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-out 0s;
}

.about__request span::before {
  transform: translate(-50%, -50%) rotate(90deg);
}

.about__request span::after {
  transform: translate(-50%, -50%) rotate(0deg);
}

.about__request:hover span::before {
  transform: translate(-50%, -50%) rotate(360deg);
}

.about__request:hover span::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.about__benefits {
  list-style: none;
}

.about__benefit-title {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
  color: #ECB869;
}

.about__benefit-text {
  font-size: 16px;
  line-height: 18px;
}

.about__benefit + .about__benefit {
  margin-top: 25px;
}

@media screen and (max-width: 1600px) {
  .about {
    padding-top: 100px;
  }
}

@media screen and (max-width: 1260px) {
  .about {
    padding-top: 70px;
  }
  .about__header {
    margin-bottom: 25px;
  }
  .about__body + .about__benefits {
    margin-left: 50px;
  }
}

@media screen and (max-width: 780px) {
  .about {
    padding-top: 160px;
  }
  .about__wrap {
    justify-content: center;
    align-items: center;
  }
  .about__benefits {
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .about__logo {
    max-width: 80px;
  }
  .about__slogan {
    font-size: 10px;
    line-height: 12px;
  }
  .about__header {
    margin-bottom: 15px;
  }
  .about__content {
    font-size: 14px;
  }
}

@media screen and (max-width: 410px) {
  .about__content {
    line-height: 19px;
  }
}

@media screen and (max-width: 400px) and (max-height: 750px) {
  .about {
    overflow-y: auto;
    padding-bottom: 70px;
    padding-top: 130px;
  }
}
