@import "./varaibles";

.form {
    width: 100%;
    &__row {
        width: 100%;
        display: flex;
        flex-wrap: wrap; }
    &__col {
        flex-grow: 1;
        min-width: 150px;
        margin-bottom: 30px;
        margin-right: 20px; }
    &__input {
        border-bottom: 1px solid #595B61;
        position: relative;
        width: 100%;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            bottom: -1px;
            left: 0;
            height: 1px;
            z-index: 2;
            width: 100%;
            transform-origin: left;
            transform: scaleX(0);
            @include transition(transform); }
        &_focused {
            &::after {
                transform: scaleX(1);
                background-color: #fff; } }
        &_err {
            color: #ec3636;
            &::after {
                transform: scaleX(1);
                background-color: #ec3636; } }
        input {
            font-size: 16px;
            padding: 12px 0px;
            display: block;
            width: 100%;
            border: none;
            color: inherit;
            background-color: transparent;
            outline: none; } }
    &__submit {
        align-self: center;
        font-size: 20px;
        text-align: left;
        padding: 10px 52px 10px 0px;
        background-image: url(../img/tip-round.svg);
        background-position: right center;
        background-repeat: no-repeat;
        background-size: 34px 34px;
        @include transition(color);
        flex-shrink: 0;
        &_mt70 {
            margin-top: 70px; }
        &_dark {
            color: $text-color-black; }
        &:hover {
            color: $text-color-accent; } }
    &_popup &__row {
        grid-template-columns: repeat(auto-fit, minmax(370px, 1fr)); }
    &_popup {
        margin-top: 70px;
        max-width: 555px; } }

@media screen and ( max-width: 1640px ) {
    .form {
        &__col {
            margin-bottom: 20px; }
        &__row {}
        &__input {
            input {
                padding: 5px 0px; } } } }

@media screen and ( max-width: 520px ) {
    .form {
        &__col {
            min-width: 200px; }
        &__input {
            input {
                font-size: 14px; } } } }

@media screen and ( max-width: 450px ) {
    .form {
        &_popup {
            margin-top: 30px; } } }
