.button-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
}

.button-list__item {
  display: inline-block;
}

.button-list__item + .button-list__item {
  margin-top: 16px;
}

.button-list__button {
  height: 30px;
  width: 30px;
  border-radius: 50% !important;
  background-color: #fff !important;
  flex-shrink: 0;
  transition: all 0.3s ease-out 0s;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}

.button-list__button svg {
  fill: #373A36;
}

.button-list__button_menu {
  background-image: url(../img/menu-icon.svg);
}

.button-list__button_phone {
  background-image: url(../img/phone.svg);
}

.button-list__button_mail {
  background-image: url(../img/mail.svg);
}

.button-list__button:hover {
  background-color: #ECB869 !important;
}

.button-list__button:focus {
  box-shadow: 0 0 10px 4px #ffffff33;
}

@media screen and (max-height: 700px) {
  .button-list__item + .button-list__item {
    margin-top: 10px;
  }
}
