@import "./varaibles";

.main {
    display: flex;
    position: relative;
    grid-area: main;
    padding-left: 370px;
    min-width: 0;
    &__control {
        position: absolute;
        color: #4F5B5F;
        font-size: 12px;
        text-align: left;
        z-index: 4;
        overflow: visible;
        @include transition(all);
        transform: translate(-50%, 0%) rotate(-90deg);
        bottom: 140px;
        left: 70px;
        padding: 5px 0px;
        pointer-events: all;
        visibility: visible;
        svg {
            position: absolute;
            top: 50%;
            left: -20px;
            fill: #4F5B5F;
            stroke: #4F5B5F;
            transform: translate(-100%, -50%) rotate(90deg);
            @include transition(fill);
            transition-duration: .2s; }
        &:hover {
            color: $text-color-accent;
            svg {
                fill: $text-color-accent;
                stroke: $text-color-accent; } }
        &_hidden {
            opacity: 0;
            pointer-events: none;
            visibility: hidden; }
        &_back svg {
            transform: translate(-100%, -50%) rotate(-90deg); } }
    & > .swiper-wrapper > .swiper-slide {
        @include transition(opacity);
        opacity: 0;
        &-active {
            opacity: 1; } } }

@media screen and ( max-width: 1300px ) {
    .main {
        padding-left: 100px; } }

@media screen and ( max-width: 1060px ) {
    .main {
        padding-left: 0px;
        &__control {
            transform: translate(-100%, 180%) rotate(-90deg);
            &_back {
                transform: translate(-150%, 180%) rotate(-90deg); } } } }

@media screen and ( max-width: 920px ) {
    .main {
        padding-top: 80px;
        overflow: hidden;
        &__control {
            font-size: 0;
            width: 24px;
            height: 35px;
            background-image: url(../img/slide-next-m.png);
            background-size: cover;
            background-repeat: no-repeat;
            transform: translate(0%, 0%) rotate(0deg);
            left: 15px;
            bottom: 20px;
            &_back {
                background-image: url(../img/slide-back-m.png);
                transform: translate(0%, 0%) rotate(0deg); }
            svg {
                display: none; } } } }

@media screen and ( max-height: 700px ) {
    .main {
        padding-top: 10px; } }
