.hero {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__body {
  width: 100%;
  max-width: 820px;
}

.hero__title {
  font-size: 68px;
  font-weight: 700;
  color: #ffffff;
}

.hero__list {
  margin-top: 55px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  list-style-type: none;
}

.hero__item {
  max-width: 210px;
  font-size: 22px;
  color: #ECB869;
}

@media screen and (max-width: 1060px) {
  .hero__title {
    font-size: 52px;
    line-height: 62px;
    max-width: 600px;
  }
  .hero__list {
    margin-top: 35px;
  }
}

@media screen and (max-width: 640px) {
  .hero__title {
    font-size: 42px;
    line-height: 52px;
    max-width: 500px;
  }
  .hero__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .hero__item {
    max-width: none;
  }
  .hero__item + .hero__item {
    margin-top: 15px;
  }
}

@media screen and (max-width: 410px) {
  .hero__title {
    font-size: 32px;
    line-height: 40px;
  }
  .hero__list {
    margin-top: 25px;
  }
  .hero__item {
    font-size: 18px;
  }
}

@media screen and (max-height: 700px) {
  .hero {
    padding-top: 60px;
  }
}
