.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0px 15px;
  position: relative;
}

.container_header {
  max-width: 1710px;
  padding-right: 80px;
}

.container_shrink {
  width: auto;
}

.container_outsource, .container_reviews {
  max-width: 950px;
}

.container_outsource {
  margin-bottom: 95px;
  margin-top: auto;
}

.container_full-height {
  height: 100%;
}

.container_large {
  max-width: 1620px;
  margin-left: -120px;
}

.container_medium {
  max-width: 1270px;
}

@media screen and (max-width: 1300px) {
  .container_large {
    margin-left: 0;
  }
}

@media screen and (max-width: 1060px) {
  .container_header {
    padding-left: 15px;
  }
}

@media screen and (max-width: 920px) {
  .container_outsource {
    margin-bottom: 0px;
    margin-top: 0px;
  }
}
