@import "./varaibles";

.popup {
    position: fixed;
    left: 0;
    top: 0;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(0, 0, 0, .5);
    @include transition(all);
    display: flex;
    &__title {
        color: #000000;
        font-size: 30px;
        font-weight: 500;
        line-height: 42px; }
    &__subtitle {
        color: #000000;
        font-size: 20px;
        font-weight: 500;
        line-height: 28px; }
    &__text {
        font-size: 16px;
        line-height: 28px;
        margin-top: 5px;
        color: #000000; }
    &__partners {
        margin-top: 75px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
        max-width: 535px;
        img {
            margin-bottom: 50px; } }
    &__close {
        flex-shrink: 0;
        height: 30px;
        width: 30px;
        background-color: $text-color-accent;
        border-radius: 50%;
        &::before, &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            left: 50%;
            height: 2px;
            width: 14px;
            background-color: #000000; }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg); } }
    &__body {
        background-color: #fff;
        max-width: 825px;
        height: 100%;
        width: 100%;
        margin-left: auto;
        position: relative;
        transform: translateX(100%);
        @include transition(transform);
        padding: 80px 84px;
        overflow-y: auto;
        &_small-padding {
            padding: 80px 25px 80px 80px; } }
    &__list {
        margin-top: 75px;
        list-style-type: none;
        li + li {
            margin-top: 1.3em; }
        li {
            position: relative;
            color: #000000;
            padding-left: 30px;
            &::before {
                content: '';
                position: absolute;
                display: inline-block;
                height: 9px;
                width: 9px;
                border-radius: 50%;
                background-color: #000;
                left: 4px;
                top: 5px; } } }
    &__request {
        text-align: left;
        font-size: 16px;
        font-weight: 400;
        color: #000000;
        overflow: visible;
        @include transition(color);
        &-deco {
            height: 25px;
            width: 25px;
            border-radius: 50%;
            background-color: $text-color-accent;
            display: inline-block;
            vertical-align: middle;
            margin-right: 20px;
            position: relative;
            transform: translateY(-3px);
            &::after, &::before {
                content: '';
                display: inline-block;
                height: 2px;
                width: 14px;
                background-color: #000000;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%); }
            &::before {
                width: 2px;
                height: 14px; } }
        &:hover {
            color: $text-color-accent; } }
    &__row {
        margin-top: 30px;
        display: flex;
        justify-content: space-between; }
    &__cities {
        list-style-type: none;
        & + & {
            margin-left: 20px; } }
    &__map {
        position: relative;
        img {
            width: 100%;
            height: auto; }
        &-wrapper {
            margin-top: 10px; } }
    &__city {
        color: #9CB0B7;
        font-size: 12px;
        font-weight: 400;
        line-height: 20px;
        cursor: pointer;
        @include transition(color);
        outline: none;
        &:hover, &.active {
            color: $text-color-accent; }
        &:focus {
            text-decoration: underline; } }
    &__marks {
        list-style-type: none;
        left: 0;
        top: 0; }
    &__mark {
        position: absolute;
        left: 0%;
        top: 0%;
        font-size: 12px;
        font-weight: 700;
        padding-left: 20px;
        @include transition(color);
        color: transparent;
        cursor: pointer;
        outline: none;
        &::before {
            content: '';
            display: inline-block;
            position: absolute;
            left: 0;
            top: 4px;
            width: 14px;
            height: 20px;
            background-image: url(../img/map-tip.svg);
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            transform-origin: bottom;
            @include transition(transform); }
        &.active, &.visible, &:hover, &:focus {
            color: $text-orange;
            z-index: 3;
            &::before {
                transform: translateY(4px) scale(2); } }
        &:nth-child(1) {
            left: 48%;
            top: 13%; }
        &:nth-child(2) {
            left: 49%;
            top: 23%; }
        &:nth-child(3) {
            left: 75.5%;
            top: 30%; }
        &:nth-child(4) {
            left: 75%;
            top: 35%; }
        &:nth-child(5) {
            left: 77%;
            top: 27%; }
        &:nth-child(6) {
            left: 77%;
            top: 34%; }
        &:nth-child(7) {
            left: 69.4%;
            top: 39%; }
        &:nth-child(8) {
            left: 71%;
            top: 43%; }
        &:nth-child(9) {
            left: 69.4%;
            top: 47%; }
        &:nth-child(10) {
            left: 68.5%;
            top: 45.5%; }
        &:nth-child(11) {
            left: 70.5%;
            top: 51%; }
        &:nth-child(12) {
            left: 62%;
            top: 37%; }
        &:nth-child(13) {
            left: 62.7%;
            top: 29%; }
        &:nth-child(14) {
            left: 66%;
            top: 36%; }
        &:nth-child(15) {
            left: 71.6%;
            top: 52%; }
        &:nth-child(16) {
            left: 79.6%;
            top: 25%; }
        &:nth-child(17) {
            left: 80%;
            top: 24%; }
        &:nth-child(18) {
            left: 79%;
            top: 26%; }
        &:nth-child(19) {
            left: 79.5%;
            top: 26.5%; }
        &:nth-child(20) {
            left: 6.4%;
            top: 29%; }
        &:nth-child(21) {
            left: 16%;
            top: 23%; }
        &:nth-child(22) {
            left: 19%;
            top: 24%; }
        &:nth-child(23) {
            left: 83.5%;
            top: 77%; } }
    &_more &__container {
        max-height: 810px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .popup__request {
            margin-top: auto; } }
    &_map {
        .popup {
            &__close {
                position: absolute;
                left: 20px;
                top: 20px;
                z-index: 6; }
            &__body {
                max-width: none; } }
        iframe {
            filter: grayscale(70%);
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0; } }
    &.active &__body {
        transform: translateX(0); }
    &.active {
        opacity: 1;
        pointer-events: all;
        visibility: visible; }
    .form {
        &__col {
            min-width: 270px;
            flex-shrink: 0; }
        &__input input {
            color: #000;
            font-size: 20px; } } }

@media screen and ( max-width: 920px ) {
    .popup {
        &__body {
            max-width: 95vw; }
        &__title {
            margin-bottom: 10px;
            br {
                display: none; } }
        &__subtitle {
            margin-bottom: 30px; }
        &__text {
            margin-bottom: 70px; }
        &__list {
            margin-top: -30px; }
        &__partners {
            margin-top: 0;
            img {
                margin-bottom: 40px;
                &:nth-child(n+6) {
                    display: none; } } }
        &__map {
            display: none; } } }

@media screen and ( max-width: 720px ) {
    .popup {
        &__body {
            padding: 40px 50px; }
        .form {
            &__input input {
                color: #000;
                font-size: 16px; } } } }

@media screen and ( max-width: 450px ) {
    .popup {
        &__title {
            font-size: 20px;
            line-height: 30px; }
        &__text {
            font-size: 14px;
            line-height: 21px; }
        &__body {
            padding: 35px 20px; }
        .popup__close {
            margin-bottom: 50px !important; } } }


