@import "./varaibles";

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border-radius: 0px;
    font-family: 'Cera Pro', sans-serif;
    border: none;
    color: $text-color; }

img {
    max-width: 100%; }

a {
    text-decoration: none; }

body, html {
    overflow: hidden;
    background-color: $body-bg;
    background-color: #000000;
    &.no-scroll {
        overflow: hidden; } }

section {
    position: relative;
    &:not(.swiper-slide-active) {
        pointer-events: none; } }

@media screen and ( max-width: 920px ) {
    body, html {
        overflow: visible;
        overflow-x: hidden; } }
