@import "./varaibles";

.reviews {
    min-width: 0;
    padding-top: 40px;
    &__swiper {
        margin-bottom: 90px; }
    &__title {
        margin-bottom: 140px; }
    &__body {
        min-width: 0; }
    &__slide {
        @include transition(opacity);
        &:not(.swiper-slide-active) {
            opacity: 0; } }
    &__buttons {
        display: flex;
        align-items: center; }
    &__control {
        height: 30px;
        width: 30px;
        border-radius: 50%;
        background-color: #fff;
        @include transition(background-color);
        background-size: 30%;
        background-image: url(../img/swiper-arrow.svg);
        background-repeat: no-repeat;
        background-position: 55% center;
        &_prev {}
        &_next {
            margin-left: 10px;
            transform: rotate(180deg); }
        &:hover {
            background-color: $text-color-accent; } } }

@media screen and ( max-width: 1590px ) {
    .reviews {
        padding-top: 40px;
        &__title {
            margin-bottom: 80px; }
        &__swiper {
            margin-bottom: 50px; } } }

@media screen and ( max-width: 920px ) {
    .reviews {
        padding-left: 0;
        padding-top: 170px;
        &__title {
            display: none; }
        &__swiper {
            max-width: 640px;
            margin: 0px auto 20px; }
        &__buttons {
            justify-content: center; } } }

@media screen and ( max-height: 700px ) {
    .reviews {
        padding-top: 130px; } }
