@import "./varaibles";

.contacts {
    padding-top: 170px;
    &__wrap {
        display: flex; }
    &__list {
        max-width: 380px;
        width: 100%;
        list-style-type: none;
        padding-top: 25px; }
    &__item {
        & + & {
            margin-top: 63px; } }
    &__link {
        display: inline-block;
        font-size: 22px;
        font-weight: 400;
        padding-left: 40px;
        background-repeat: no-repeat;
        background-position: left center;
        background-size: 22px 22px; }
    &__copyright {
        position: absolute;
        bottom: 40px;
        font-size: 14px;
        color: #B2B2B2;
        right: 0; }
    &__map {
        position: relative;
        background-color: #fdfdfd;
        max-width: 630px;
        width: 100%;
        padding-bottom: 26.3%;
        margin-left: 110px;
        &-trigger {
            display: none;
            margin-top: 25px;
            text-align: left;
            font-size: 15px;
            color: $text-color-grey;
            padding-bottom: 3px;
            @include transition(color);
            &::after {
                content: '';
                display: inline-block;
                position: absolute;
                width: 100%;
                bottom: 1px;
                left: 0;
                height: 1px;
                background-color: $text-color-grey;
                @include transition(background-color); } }
        & > * {
            filter: grayscale(70%);
            position: absolute;
            width: 100%;
            height: 100%; } }
    &__form {
        display: flex;
        align-items: flex-end;
        margin-top: 75px;
        background: rgba(19, 24, 28, 0.68);
        max-width: 1210px;
        padding: 20px 40px 20px 0px;
        position: relative;
        &::before {
            content: '';
            display: block;
            width: 400px;
            height: 100%;
            position: absolute;
            right: 100%;
            top: 0;
            background: rgba(19, 24, 28, 0.68); }
        &-text {
            max-width: 300px;
            font-weight: 400; }
        &-title {
            font-size: 35px;
            line-height: 37px;
            font-weight: 300;
            max-width: 280px;
            margin-bottom: 14px; }
        &-body {
            margin: 0px 43px 0 63px;
            width: 100%;
            max-width: 600px; } } }

@media screen and ( max-width: 1640px ) {
    .contacts {
        &__form {
            margin-top: 20px;
            padding: 15px 30px;
            &-title {
                font-size: 28px;
                line-height: 32px; }
            &-body {
                margin: 0px 30px; } } } }

@media screen and ( max-width: 1260px ) {
    .contacts {
        padding-top: 70px;
        &__map {
            min-height: 250px;
            margin-left: 30px; } } }

@media screen and ( max-width: 920px ) {
    .contacts {
        padding-bottom: 40px;
        &__copyright {
            font-size: 10px;
            text-align: center;
            display: inline-block;
            bottom: 5px;
            right: auto;
            left: 50%;
            transform: translateX(-50%);
            width: 100%; }
        &__list {
            padding-top: 0; }
        &__link {
            font-size: 18px; }
        &__item {
            & + & {
                margin-top: 30px; } }
        &__wrap {
            flex-direction: column;
            align-items: flex-start;
            padding-left: 15px; }
        &__form {
            padding-bottom: 50px; }
        &__map {
            display: none;
            &-trigger {
                display: inline-block; } } } }

@media screen and ( max-width: 895px ) {
    .contacts {
        padding-top: 100px;
        &__form {
            flex-direction: column;
            align-items: flex-start;
            margin-right: 20px;
            .form__submit {
                align-self: initial; }
            &-body {
                margin: 25px 0px 10px; }
            &-title {
                font-size: 20px;
                line-height: 26px; }
            &-title, &-text {
                max-width: none; }
            &-subtitle {
                display: none; } } } }

@media screen and ( max-width: 520px ) {
    .contacts {
        &__map {
            &-trigger {
                margin-top: 15px; } }
        &__form {
            padding-bottom: 80px;
            .form__submit {
                align-self: center; } } } }

@media screen and (max-height: 700px) {
    .contacts {
        padding-top: 110px;
        &__item {
            & + & {
                margin-top: 10px; } } } }

@media screen and (max-height: 600px) {
    .contacts {
        overflow-y: auto; } }
