.nav {
  width: 100%;
  max-width: 1120px;
  padding: 0px 15px 0px 60px;
  transition: all 0.3s ease-out 0s;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  list-style-type: none;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  text-transform: uppercase;
  position: relative;
  font-size: 13px;
  color: #ffffff;
  padding: 5px 0px;
  display: inline-block;
  transition: color 0.3s ease-out 0s;
}

.nav__link_no-text-transform {
  text-transform: none;
}

.nav__link::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.3s ease-out 0s;
  background-color: #ffffff;
}

.nav__link:hover {
  color: #ECB869;
}

.nav__link:hover::after {
  transform: scaleX(0.5);
  background-color: #ECB869;
}

.nav_hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-40px);
}

@media screen and (max-width: 1060px) {
  .nav {
    display: none;
  }
}
