.aside {
  grid-area: aside;
  padding: 45px 0px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 6;
}

@media screen and (max-width: 1060px) {
  .aside {
    padding: 20px 0px 30px;
    position: fixed;
    right: 15px;
    top: 0;
    grid-area: initial;
  }
}
