@import "./varaibles";

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0px 15px;
    position: relative;
    &_header {
        max-width: 1710px;
        padding-right: 80px; }
    &_shrink {
        width: auto; }
    &_outsource, &_reviews {
        max-width: 950px; }
    &_outsource {
        margin-bottom: 95px;
        margin-top: auto; }
    &_full-height {
        height: 100%; }
    &_large {
        max-width: 1620px;
        margin-left: -120px; }
    &_medium {
        max-width: 1270px; } }

@media screen and ( max-width: 1300px ) {
    .container {
        &_large {
            margin-left: 0; } } }

@media screen and ( max-width: 1060px ) {
    .container {
        &_header {
            padding-left: 15px; } } }

@media screen and ( max-width: 920px ) {
    .container {
        &_outsource {
            margin-bottom: 0px;
            margin-top: 0px; } } }
