.service-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.service-item__title {
  font-size: 30px;
  font-weight: 500;
  line-height: 42px;
  margin-bottom: 20px;
}

.service-item__descr {
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 30px;
  display: inline-block;
  color: #979CA1;
  width: 100%;
}

.service-item__bottom {
  width: 100%;
  margin-top: auto;
  max-width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.service-item__request {
  font-weight: 500;
  font-size: 14px;
  color: #ECB869;
}

.service-item__request-deco {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  margin-right: 8px;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  background-color: #ECB869;
  transform: translateY(-2px);
}

.service-item__request-deco::before, .service-item__request-deco::after {
  content: '';
  display: inline-block;
  height: 1px;
  width: 7px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-out 0s;
  transition-duration: .35s;
}

.service-item__request-deco::before {
  transform: translate(-50%, -50%) rotate(0deg);
}

.service-item__request-deco::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.service-item__request:hover .service-item__request-deco::before {
  transform: translate(-50%, -50%) rotate(360deg);
}

.service-item__request:hover .service-item__request-deco::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.service-item__more {
  color: #807F85;
  font-weight: 400;
  font-size: 14px;
  display: inline-block;
  padding: 3px 0px;
  position: relative;
  transition: color 0.3s ease-out 0s;
}

.service-item__more::after {
  content: '';
  display: inline-block;
  height: 1px;
  width: 100%;
  bottom: 2px;
  left: 0;
  transform-origin: left;
  position: absolute;
  background-color: #807F85;
  transition: background-color 0.3s ease-out 0s;
}

.service-item__more:hover {
  color: #ECB869;
}

.service-item__more:hover::after {
  background-color: #ECB869;
}

@media screen and (max-width: 920px) {
  .service-item__title {
    font-size: 32px;
    line-height: 40px;
  }
  .service-item__descr {
    display: none;
  }
}

@media screen and (max-width: 640px) {
  .service-item__title {
    font-size: 19px;
    line-height: 23px;
    margin-bottom: 15px;
  }
}

@media screen and (max-height: 600px) {
  .service-item__title {
    margin-bottom: 5px;
  }
}
