.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-height: 360px;
  list-style-type: none;
  position: relative;
}

.pagination__item {
  display: inline-block;
  flex-shrink: 0;
  padding: 12px 10px;
  cursor: pointer;
  position: relative;
  border-radius: 50%;
  outline: none;
}

.pagination__item-slide {
  height: 15px;
  width: 15px;
  border-radius: 50px;
  flex-shrink: 0;
  position: absolute;
  background-color: transparent;
  border: 1px solid #B2B6B9;
  pointer-events: none;
  transform: translate(17%, 30%);
  transition: all 0.3s ease-out 0s;
}

.pagination__item span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  background-color: #B2B6B9;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  pointer-events: none;
}

@media screen and (max-width: 1060px) {
  .pagination {
    display: none;
  }
}
