@import "./varaibles";

.outsource {
    display: flex;
    &__title {
        margin-bottom: 50px; }
    &__content {
        font-size: 18px;
        line-height: 150%;
        ul {
            list-style-type: none;
            margin-bottom: 1.2em; }
        li {
            &::before {
                content: '-';
                margin-right: .7em; } } } }

@media screen and ( max-width: 920px ) {
    .outsource {
        padding-left: 0;
        padding-top: 140px;
        &__content {
            font-size: 14px; }
        .section-title {
            font-size: 24px;
            line-height: 32px;
            margin-bottom: 25px; } } }

@media screen and ( max-width: 720px ) {
    .outsource {
        padding-top: 110px;
        &__content {
            ul {
                margin-bottom: 1em; } }
        .section-title {
            margin-bottom: 10px; } } }

@media screen and (max-height: 700px) {
    .outsource {
        padding-top: 140px;
        &__content {
            font-size: 12px; }
        .section-title {
            font-size: 18px;
            line-height: 22px;
            margin-bottom: 15px; } } }

@media screen and (max-height: 600px) {
    .outsource {
        overflow-y: auto;
        &__content {
            padding-bottom: 60px; } } }
