@import "./varaibles";

.review-item {
    &__top {
        display: flex;
        align-items: center;
        margin-bottom: 55px; }
    &__img {
        width: 130px;
        height: 130px;
        flex-shrink: 0;
        border-radius: 100%;
        overflow: hidden;
        object-fit: cover; }
    &__img + &__data {
        margin-left: 45px; }
    &__name {
        font-size: 25px;
        font-weight: 400;
        margin-bottom: 5px; }
    &__info {
        display: block;
        font-size: 16px;
        line-height: 24px; }
    &__body {
        p {
            font-size: 18px;
            line-height: 150%; } } }

@media screen and ( max-width: 920px ) {
    .review-item {
        &__top {
            margin-bottom: 30px; }
        &__info {
            font-size: 14px; }
        &__img {
            width: 90px;
            height: 90px; }
        &__img + &__data {
            margin-left: 20px; }
        &__name {
            font-size: 18px;
            line-height: 24px; }
        &__body {
            p {
                font-size: 14px; } } } }

@media screen and ( max-width: 400px ) and (max-height: 750px) {
    .review-item {
        &__top {
            margin-bottom: 15px; }
        &__body {
            p {
                font-size: 12px; } } } }
