.review-item__top {
  display: flex;
  align-items: center;
  margin-bottom: 55px;
}

.review-item__img {
  width: 130px;
  height: 130px;
  flex-shrink: 0;
  border-radius: 100%;
  overflow: hidden;
  object-fit: cover;
}

.review-item__img + .review-item__data {
  margin-left: 45px;
}

.review-item__name {
  font-size: 25px;
  font-weight: 400;
  margin-bottom: 5px;
}

.review-item__info {
  display: block;
  font-size: 16px;
  line-height: 24px;
}

.review-item__body p {
  font-size: 18px;
  line-height: 150%;
}

@media screen and (max-width: 920px) {
  .review-item__top {
    margin-bottom: 30px;
  }
  .review-item__info {
    font-size: 14px;
  }
  .review-item__img {
    width: 90px;
    height: 90px;
  }
  .review-item__img + .review-item__data {
    margin-left: 20px;
  }
  .review-item__name {
    font-size: 18px;
    line-height: 24px;
  }
  .review-item__body p {
    font-size: 14px;
  }
}

@media screen and (max-width: 400px) and (max-height: 750px) {
  .review-item__top {
    margin-bottom: 15px;
  }
  .review-item__body p {
    font-size: 12px;
  }
}
