.burger {
  width: 40px;
  height: 40px;
  flex-shrink: 0;
  display: none !important;
}

.burger span, .burger::before, .burger::after {
  content: '';
  display: inline-block;
  width: 25px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: top 0.3s ease-in-out 0.3s, transform 0.3s ease-in-out 0s, opacity 0.1s ease-in-out 0.3s;
}

.burger::before {
  top: calc( 9px + 50%);
}

.burger::after {
  top: calc( -9px + 50%);
}

.burger span {
  transform: translate(-50%, -50%);
}

.burger.active span, .burger.active::before, .burger.active::after {
  transform: translate(-50%, -50%);
  top: 50%;
  transition: top 0.3s ease-in-out 0s, transform 0.3s ease-in-out 0.3s, opacity 0.3s ease-in-out 0.1s;
}

.burger.active span {
  opacity: 0;
}

.burger.active::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.burger.active::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
