.faq {
  padding-top: 130px;
}

.faq__wrap {
  display: flex;
}

.faq__questions {
  list-style-type: none;
}

.faq__trigger {
  max-width: 510px;
  padding: 15px 25px;
  font-size: 19px;
  line-height: 30px;
  text-align: left;
  transition: all 0.3s ease-out 0s;
}

.faq__trigger.active, .faq__trigger:hover {
  color: #ECB869;
  background-color: rgba(19, 24, 28, 0.68);
}

.faq__question + .faq__question {
  margin-top: 35px;
}

.faq__answers-list {
  list-style-type: none;
}

.faq__answer {
  position: absolute;
  height: 100%;
  background-color: #fff;
  max-width: 560px;
  width: 100%;
  padding: 0px 50px;
  transition: opacity 0.3s ease-out 0s;
}

.faq__answer.active {
  z-index: 3;
}

.faq__answer .popup__close {
  display: none;
}

.faq__answer:not(.active) {
  opacity: 0;
}

.faq__answer::before {
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  height: 400px;
  left: 0;
  bottom: 100%;
  background-color: inherit;
  z-index: -1;
}

.faq__answer-controls {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.faq__answer-control {
  display: none;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #DBDBDB;
  transition: background-color 0.3s ease-out 0s;
  background-size: 30%;
  background-image: url(../img/swiper-arrow.svg);
  background-repeat: no-repeat;
  background-position: 55% center;
}

.faq__answer-control_next {
  margin-left: 10px;
  transform: rotate(180deg);
}

.faq__answer-control:hover {
  background-color: #ECB869;
}

.faq__answer-title {
  font-size: 16px;
  font-weight: 500;
  color: #ECB869;
  margin-bottom: 3px;
}

.faq__answer-content {
  font-size: 16px;
  line-height: 210%;
  color: #000000;
}

.faq__answer-content p {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
}

.faq__answer-content p + p {
  margin-top: 1.2em;
}

@media screen and (max-width: 1360px) {
  .faq {
    margin-left: 0px;
  }
}

@media screen and (max-width: 1260px) {
  .faq {
    margin-left: 0px;
  }
  .faq__questions {
    max-width: 440px;
  }
  .faq__answer {
    max-width: 440px;
    padding: 0px 30px;
  }
  .faq__question + .faq__question {
    margin-top: 10px;
  }
}

@media screen and (max-width: 1060px) {
  .faq {
    margin-left: 0px;
  }
  .faq__trigger {
    font-size: 16px;
    line-height: 26px;
  }
}

@media screen and (max-width: 920px) {
  .faq {
    padding-top: 130px;
    position: relative;
  }
  .faq__wrap {
    flex-direction: column;
    align-items: stretch;
  }
  .faq__questions {
    margin-right: -15px;
  }
  .faq__questions, .faq__answer {
    max-width: none;
  }
  .faq__answers-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  .faq__trigger {
    background-color: rgba(19, 24, 28, 0.68);
    width: 100%;
    max-width: none;
    font-size: 16px;
    line-height: 20px;
  }
  .faq__answer {
    display: block;
    height: 100vh;
    overflow-y: auto;
    left: 0;
    top: 0;
    z-index: 10;
    opacity: 1 !important;
    transform: translateX(100%);
    background-color: #fff;
    transition: all 0.3s ease-out 0s;
    visibility: hidden;
    padding: 35px 20px;
  }
  .faq__answer::before {
    display: none;
  }
  .faq__answer .popup__close, .faq__answer-control {
    display: inline-block;
  }
  .faq__answer.active {
    visibility: visible;
    transform: translateX(0%);
  }
  .container {
    position: static;
  }
}

@media screen and (max-width: 700px) {
  .faq {
    padding-top: 120px;
  }
}

@media screen and (max-height: 700px) {
  .faq {
    padding-top: 140px;
  }
  .faq__trigger {
    font-size: 14px;
    line-height: 18px;
  }
}
