@import "./varaibles";

.hero {
    display: flex;
    align-items: center;
    justify-content: center;
    &__body {
        width: 100%;
        max-width: 820px; }
    &__title {
        font-size: 68px;
        font-weight: 700;
        color: $text-color; }
    &__list {
        margin-top: 55px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        list-style-type: none; }
    &__item {
        max-width: 210px;
        font-size: 22px;
        color: $text-color-accent; } }

@media screen and ( max-width: 1060px ) {
    .hero {
        &__title {
            font-size: 52px;
            line-height: 62px;
            max-width: 600px; }
        &__list {
            margin-top: 35px; } } }

@media screen and ( max-width: 640px ) {
    .hero {
        &__title {
            font-size: 42px;
            line-height: 52px;
            max-width: 500px; }
        &__list {
            flex-direction: column;
            align-items: flex-start; }
        &__item {
            max-width: none;
            & + & {
                margin-top: 15px; } } } }

@media screen and ( max-width: 410px ) {
    .hero {
        &__title {
            font-size: 32px;
            line-height: 40px; }
        &__list {
            margin-top: 25px; }
        &__item {
            font-size: 18px; } } }

@media screen and ( max-height: 700px ) {
    .hero {
        padding-top: 60px; } }

