*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border-radius: 0px;
  font-family: 'Cera Pro', sans-serif;
  border: none;
  color: #ffffff;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

body, html {
  overflow: hidden;
  background-color: #242424;
  background-color: #000000;
}

body.no-scroll, html.no-scroll {
  overflow: hidden;
}

section {
  position: relative;
}

section:not(.swiper-slide-active) {
  pointer-events: none;
}

@media screen and (max-width: 920px) {
  body, html {
    overflow: visible;
    overflow-x: hidden;
  }
}
