.form {
  width: 100%;
}

.form__row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.form__col {
  flex-grow: 1;
  min-width: 150px;
  margin-bottom: 30px;
  margin-right: 20px;
}

.form__input {
  border-bottom: 1px solid #595B61;
  position: relative;
  width: 100%;
}

.form__input::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: -1px;
  left: 0;
  height: 1px;
  z-index: 2;
  width: 100%;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.3s ease-out 0s;
}

.form__input_focused::after {
  transform: scaleX(1);
  background-color: #fff;
}

.form__input_err {
  color: #ec3636;
}

.form__input_err::after {
  transform: scaleX(1);
  background-color: #ec3636;
}

.form__input input {
  font-size: 16px;
  padding: 12px 0px;
  display: block;
  width: 100%;
  border: none;
  color: inherit;
  background-color: transparent;
  outline: none;
}

.form__submit {
  align-self: center;
  font-size: 20px;
  text-align: left;
  padding: 10px 52px 10px 0px;
  background-image: url(../img/tip-round.svg);
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 34px 34px;
  transition: color 0.3s ease-out 0s;
  flex-shrink: 0;
}

.form__submit_mt70 {
  margin-top: 70px;
}

.form__submit_dark {
  color: #373A36;
}

.form__submit:hover {
  color: #ECB869;
}

.form_popup .form__row {
  grid-template-columns: repeat(auto-fit, minmax(370px, 1fr));
}

.form_popup {
  margin-top: 70px;
  max-width: 555px;
}

@media screen and (max-width: 1640px) {
  .form__col {
    margin-bottom: 20px;
  }
  .form__input input {
    padding: 5px 0px;
  }
}

@media screen and (max-width: 520px) {
  .form__col {
    min-width: 200px;
  }
  .form__input input {
    font-size: 14px;
  }
}

@media screen and (max-width: 450px) {
  .form_popup {
    margin-top: 30px;
  }
}
