.reviews {
  min-width: 0;
  padding-top: 40px;
}

.reviews__swiper {
  margin-bottom: 90px;
}

.reviews__title {
  margin-bottom: 140px;
}

.reviews__body {
  min-width: 0;
}

.reviews__slide {
  transition: opacity 0.3s ease-out 0s;
}

.reviews__slide:not(.swiper-slide-active) {
  opacity: 0;
}

.reviews__buttons {
  display: flex;
  align-items: center;
}

.reviews__control {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #fff;
  transition: background-color 0.3s ease-out 0s;
  background-size: 30%;
  background-image: url(../img/swiper-arrow.svg);
  background-repeat: no-repeat;
  background-position: 55% center;
}

.reviews__control_next {
  margin-left: 10px;
  transform: rotate(180deg);
}

.reviews__control:hover {
  background-color: #ECB869;
}

@media screen and (max-width: 1590px) {
  .reviews {
    padding-top: 40px;
  }
  .reviews__title {
    margin-bottom: 80px;
  }
  .reviews__swiper {
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 920px) {
  .reviews {
    padding-left: 0;
    padding-top: 170px;
  }
  .reviews__title {
    display: none;
  }
  .reviews__swiper {
    max-width: 640px;
    margin: 0px auto 20px;
  }
  .reviews__buttons {
    justify-content: center;
  }
}

@media screen and (max-height: 700px) {
  .reviews {
    padding-top: 130px;
  }
}
