.popup {
  position: fixed;
  left: 0;
  top: 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease-out 0s;
  display: flex;
}

.popup__title {
  color: #000000;
  font-size: 30px;
  font-weight: 500;
  line-height: 42px;
}

.popup__subtitle {
  color: #000000;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
}

.popup__text {
  font-size: 16px;
  line-height: 28px;
  margin-top: 5px;
  color: #000000;
}

.popup__partners {
  margin-top: 75px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  max-width: 535px;
}

.popup__partners img {
  margin-bottom: 50px;
}

.popup__close {
  flex-shrink: 0;
  height: 30px;
  width: 30px;
  background-color: #ECB869;
  border-radius: 50%;
}

.popup__close::before, .popup__close::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  width: 14px;
  background-color: #000000;
}

.popup__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup__body {
  background-color: #fff;
  max-width: 825px;
  height: 100%;
  width: 100%;
  margin-left: auto;
  position: relative;
  transform: translateX(100%);
  transition: transform 0.3s ease-out 0s;
  padding: 80px 84px;
  overflow-y: auto;
}

.popup__body_small-padding {
  padding: 80px 25px 80px 80px;
}

.popup__list {
  margin-top: 75px;
  list-style-type: none;
}

.popup__list li + li {
  margin-top: 1.3em;
}

.popup__list li {
  position: relative;
  color: #000000;
  padding-left: 30px;
}

.popup__list li::before {
  content: '';
  position: absolute;
  display: inline-block;
  height: 9px;
  width: 9px;
  border-radius: 50%;
  background-color: #000;
  left: 4px;
  top: 5px;
}

.popup__request {
  text-align: left;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  overflow: visible;
  transition: color 0.3s ease-out 0s;
}

.popup__request-deco {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background-color: #ECB869;
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px;
  position: relative;
  transform: translateY(-3px);
}

.popup__request-deco::after, .popup__request-deco::before {
  content: '';
  display: inline-block;
  height: 2px;
  width: 14px;
  background-color: #000000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup__request-deco::before {
  width: 2px;
  height: 14px;
}

.popup__request:hover {
  color: #ECB869;
}

.popup__row {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}

.popup__cities {
  list-style-type: none;
}

.popup__cities + .popup__cities {
  margin-left: 20px;
}

.popup__map {
  position: relative;
}

.popup__map img {
  width: 100%;
  height: auto;
}

.popup__map-wrapper {
  margin-top: 10px;
}

.popup__city {
  color: #9CB0B7;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  cursor: pointer;
  transition: color 0.3s ease-out 0s;
  outline: none;
}

.popup__city:hover, .popup__city.active {
  color: #ECB869;
}

.popup__city:focus {
  text-decoration: underline;
}

.popup__marks {
  list-style-type: none;
  left: 0;
  top: 0;
}

.popup__mark {
  position: absolute;
  left: 0%;
  top: 0%;
  font-size: 12px;
  font-weight: 700;
  padding-left: 20px;
  transition: color 0.3s ease-out 0s;
  color: transparent;
  cursor: pointer;
  outline: none;
}

.popup__mark::before {
  content: '';
  display: inline-block;
  position: absolute;
  left: 0;
  top: 4px;
  width: 14px;
  height: 20px;
  background-image: url(../img/map-tip.svg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transform-origin: bottom;
  transition: transform 0.3s ease-out 0s;
}

.popup__mark.active, .popup__mark.visible, .popup__mark:hover, .popup__mark:focus {
  color: #FF9A00;
  z-index: 3;
}

.popup__mark.active::before, .popup__mark.visible::before, .popup__mark:hover::before, .popup__mark:focus::before {
  transform: translateY(4px) scale(2);
}

