.popup_more .popup__container {
  max-height: 810px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.popup_more .popup__container .popup__request {
  margin-top: auto;
}

.popup_map .popup__close {
  position: absolute;
  left: 20px;
  top: 20px;
  z-index: 6;
}

.popup_map .popup__body {
  max-width: none;
}

.popup_map iframe {
  filter: grayscale(70%);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.popup.active .popup__body {
  transform: translateX(0);
}

.popup.active {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}

.popup .form__col {
  min-width: 270px;
  flex-shrink: 0;
}

.popup .form__input input {
  color: #000;
  font-size: 20px;
}

@media screen and (max-width: 920px) {
  .popup__body {
    max-width: 95vw;
  }
  .popup__title {
    margin-bottom: 10px;
  }
  .popup__title br {
    display: none;
  }
  .popup__subtitle {
    margin-bottom: 30px;
  }
  .popup__text {
    margin-bottom: 70px;
  }
  .popup__list {
    margin-top: -30px;
  }
  .popup__partners {
    margin-top: 0;
  }
  .popup__partners img {
    margin-bottom: 40px;
  }
  .popup__partners img:nth-child(n+6) {
    display: none;
  }
  .popup__map {
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .popup__body {
    padding: 40px 50px;
  }
  .popup .form__input input {
    color: #000;
    font-size: 16px;
  }
}

@media screen and (max-width: 450px) {
  .popup__title {
    font-size: 20px;
    line-height: 30px;
  }
  .popup__text {
    font-size: 14px;
    line-height: 21px;
  }
  .popup__body {
    padding: 35px 20px;
  }
  .popup .popup__close {
    margin-bottom: 50px !important;
  }
}
