.outsource {
  display: flex;
}

.outsource__title {
  margin-bottom: 50px;
}

.outsource__content {
  font-size: 18px;
  line-height: 150%;
}

.outsource__content ul {
  list-style-type: none;
  margin-bottom: 1.2em;
}

.outsource__content li::before {
  content: '-';
  margin-right: .7em;
}

@media screen and (max-width: 920px) {
  .outsource {
    padding-left: 0;
    padding-top: 140px;
  }
  .outsource__content {
    font-size: 14px;
  }
  .outsource .section-title {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 720px) {
  .outsource {
    padding-top: 110px;
  }
  .outsource__content ul {
    margin-bottom: 1em;
  }
  .outsource .section-title {
    margin-bottom: 10px;
  }
}

@media screen and (max-height: 700px) {
  .outsource {
    padding-top: 140px;
  }
  .outsource__content {
    font-size: 12px;
  }
  .outsource .section-title {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 15px;
  }
}

@media screen and (max-height: 600px) {
  .outsource {
    overflow-y: auto;
  }
  .outsource__content {
    padding-bottom: 60px;
  }
}
