@import "./varaibles";

.main-title {
    list-style-type: none;
    z-index: 3;
    position: fixed;
    top: 165px;
    left: 260px;
    width: 100%;
    &__item {
        position: absolute;
        display: inline-block;
        top: 0;
        left: 0;
        opacity: 0;
        pointer-events: none;
        text-transform: uppercase;
        font-size: 16px;
        color: #ffffff;
        font-weight: 500;
        @include transition(opacity);
        max-width: 165px;
        &::before {
            content: '';
            position: absolute;
            display: block;
            bottom: -8px;
            width: 100%;
            height: 3px;
            background-color: #fff;
            transform-origin: left;
            transform: scaleX(0);
            @include transition(transform); }
        &.active {
            opacity: 1;
            pointer-events: all;
            &::before {
                transform: scaleX(1); } } } }

@media screen and ( max-width: 1600px ) {
    .main-title {
        left: 180px; } }

@media screen and ( max-width: 1300px ) {
    .main-title {
        top: 120px;
        left: 15px; } }

@media screen and ( max-width: 1060px ) {
    .main-title {
        left: 15px; } }

@media screen and ( max-width: 920px ) {
    .main-title {
        top: 120px; } }

@media screen and ( max-height: 700px ) {
    .main-title {
        top: 80px;
        &__item {
            font-size: 14px;
            line-height: 18px; } } }


