@import "./varaibles";

.language {
    z-index: 2;
    .dropdown {
        &__list {
            top: 2px;
            left: 1px; }
        &__trigger {
            z-index: 3;
            position: relative;
            border-radius: 50%;
            height: 30px;
            width: 30px;
            background-color: #000000;
            overflow: hidden;
            &-text {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-transform: uppercase;
                font-size: 14px; } }
        &__item {
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 50%;
            font-size: 14px;
            height: 29px;
            width: 29px;
            overflow: hidden;
            background-color: $text-color;
            color: $text-color-black;
            @include transition(all);
            text-transform: uppercase;
            a {
                color: inherit;
                display: inline-block;
                position: absolute;
                font-size: inherit;
                text-transform: inherit;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%); }
            &:hover {
                background-color: $text-color-accent; } } } }

@for $index from 1 through 15 {
     .language {
        .dropdown__list.active .dropdown__item:nth-child(#{$index}) {
            transform: translateY(#{-130% * $index}); } } }

@media screen and ( max-width: 1060px ) {
    .language {
        display: none; } }
