.language {
  z-index: 2;
}

.language .dropdown__list {
  top: 2px;
  left: 1px;
}

.language .dropdown__trigger {
  z-index: 3;
  position: relative;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  background-color: #000000;
  overflow: hidden;
}

.language .dropdown__trigger-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-size: 14px;
}

.language .dropdown__item {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  font-size: 14px;
  height: 29px;
  width: 29px;
  overflow: hidden;
  background-color: #ffffff;
  color: #373A36;
  transition: all 0.3s ease-out 0s;
  text-transform: uppercase;
}

.language .dropdown__item a {
  color: inherit;
  display: inline-block;
  position: absolute;
  font-size: inherit;
  text-transform: inherit;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.language .dropdown__item:hover {
  background-color: #ECB869;
}

.language .dropdown__list.active .dropdown__item:nth-child(1) {
  transform: translateY(-130%);
}

.language .dropdown__list.active .dropdown__item:nth-child(2) {
  transform: translateY(-260%);
}

.language .dropdown__list.active .dropdown__item:nth-child(3) {
  transform: translateY(-390%);
}

.language .dropdown__list.active .dropdown__item:nth-child(4) {
  transform: translateY(-520%);
}

.language .dropdown__list.active .dropdown__item:nth-child(5) {
  transform: translateY(-650%);
}

.language .dropdown__list.active .dropdown__item:nth-child(6) {
  transform: translateY(-780%);
}

.language .dropdown__list.active .dropdown__item:nth-child(7) {
  transform: translateY(-910%);
}

.language .dropdown__list.active .dropdown__item:nth-child(8) {
  transform: translateY(-1040%);
}

.language .dropdown__list.active .dropdown__item:nth-child(9) {
  transform: translateY(-1170%);
}

.language .dropdown__list.active .dropdown__item:nth-child(10) {
  transform: translateY(-1300%);
}

.language .dropdown__list.active .dropdown__item:nth-child(11) {
  transform: translateY(-1430%);
}

.language .dropdown__list.active .dropdown__item:nth-child(12) {
  transform: translateY(-1560%);
}

.language .dropdown__list.active .dropdown__item:nth-child(13) {
  transform: translateY(-1690%);
}

.language .dropdown__list.active .dropdown__item:nth-child(14) {
  transform: translateY(-1820%);
}

.language .dropdown__list.active .dropdown__item:nth-child(15) {
  transform: translateY(-1950%);
}

@media screen and (max-width: 1060px) {
  .language {
    display: none;
  }
}
