@import "./varaibles";

.service-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__title {
        font-size: 30px;
        font-weight: 500;
        line-height: 42px;
        margin-bottom: 20px; }
    &__descr {
        font-size: 18px;
        font-weight: 500;
        line-height: 150%;
        margin-bottom: 30px;
        display: inline-block;
        color: $text-color-grey;
        width: 100%; }
    &__bottom {
        width: 100%;
        margin-top: auto;
        max-width: 300px;
        display: flex;
        align-items: center;
        justify-content: space-between; }
    &__request {
        font-weight: 500;
        font-size: 14px;
        color: $text-color-accent;
        &-deco {
            vertical-align: middle;
            display: inline-block;
            position: relative;
            margin-right: 8px;
            border-radius: 50%;
            height: 16px;
            width: 16px;
            background-color: $text-color-accent;
            transform: translateY(-2px);
            &::before, &::after {
                content: '';
                display: inline-block;
                height: 1px;
                width: 7px;
                background-color: #fff;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                @include transition(transform);
                transition-duration: .35s; }
            &::before {
                transform: translate(-50%, -50%) rotate(0deg); }
            &::after {
                transform: translate(-50%, -50%) rotate(90deg); } }
        &:hover &-deco {
            &::before {
                transform: translate(-50%, -50%) rotate(360deg); }
            &::after {
                transform: translate(-50%, -50%) rotate(90deg); } } }
    &__more {
        color: $text-color-dark-grey;
        font-weight: 400;
        font-size: 14px;
        display: inline-block;
        padding: 3px 0px;
        position: relative;
        @include transition(color);
        &::after {
            content: '';
            display: inline-block;
            height: 1px;
            width: 100%;
            bottom: 2px;
            left: 0;
            transform-origin: left;
            position: absolute;
            background-color: $text-color-dark-grey;
            @include transition(background-color); }
        &:hover {
            color: $text-color-accent;
            &::after {
                background-color: $text-color-accent; } } } }

@media screen and ( max-width: 920px ) {
    .service-item {
        &__title {
            font-size: 32px;
            line-height: 40px; }
        &__descr {
            display: none; } } }

@media screen and ( max-width: 640px ) {
    .service-item {
        &__title {
            font-size: 19px;
            line-height: 23px;
            margin-bottom: 15px; } } }

@media screen and (max-height: 600px) {
    .service-item {
        &__title {
            margin-bottom: 5px; } } }
